import React, { lazy, Suspense } from 'react';
import { Routes, Route, Navigate } from 'react-router-dom';

const SharedLayout = lazy(() => import('components/SharedLayout/SharedLayout')); // done
const Home = lazy(() => import('../pages/Home'));
const Movies = lazy(() => import('../pages/Movies'));
const MovieDetails = lazy(() => import('../pages/MovieDetails/MovieDetails'));
const Cast = lazy(() => import('../components/Cast/Cast'));
const Reviews = lazy(() => import('../components/Reviews/Reviews'));

const Error = () => {
  return <h3>Wystpi bd podczas adowania komponentu.</h3>;
};

export const App = () => {
  return (
    <Suspense fallback={<Error />}>
      <Routes>
        <Route path="/" element={<SharedLayout />}>
          <Route path="/" element={<Home />} />
          <Route path="/movies" element={<Movies />} />
          <Route path="/movies/:movieId" element={<MovieDetails />}>
            <Route path="cast" element={<Cast />} />
            <Route path="reviews" element={<Reviews />} />
          </Route>
          <Route path="*" element={<Navigate to="/" />} />
        </Route>
      </Routes>
    </Suspense>
  );
};



//  Lazy pozwala na opnione adowanie komponentw. Moesz uy do dynamicznego importowania 
// komponentw = te komponenty bd pobrane dopiero w momencie, gdy zostan uyte.

// Suspense to komponent, ktry pozwala na zdefiniowanie fallbacku 
// (czego, co bdzie pokazywane podczas adowania opnionego komponentu)
// i otaczanie miejsc, gdzie uywasz lazy